"use client";

import { useState } from 'react';
import { Flashcard as FlashcardType } from '@/lib/flashcards';
import { Card, CardContent } from '@/components/ui/card';

interface FlashcardProps {
  card: FlashcardType;
}

export function Flashcard({ card }: FlashcardProps) {
  const [isFlipped, setIsFlipped] = useState(false);

  const handleFlip = () => {
    setIsFlipped(!isFlipped);
  };

  return (
    <div 
      className="w-full h-[300px] cursor-pointer"
      style={{ perspective: '1000px' }}
      onClick={handleFlip}
      role="button"
      tabIndex={0}
      onKeyDown={(e) => {
        if (e.key === 'Enter' || e.key === ' ') {
          handleFlip();
        }
      }}
      aria-label={`Flashcard for ${card.word}`}
    >
      <div 
        className={`relative w-full h-full transition-all duration-500 ${isFlipped ? '[transform:rotateY(180deg)]' : ''}`}
        style={{ transformStyle: 'preserve-3d' }}
      >
        {/* Front of card */}
        <Card className="absolute w-full h-full" style={{ backfaceVisibility: 'hidden' }}>
          <CardContent className="h-full flex flex-col items-center justify-center">
            <h2 className="text-3xl font-bold text-gray-900 mb-4">{card.word}</h2>
            <p className="text-gray-600 text-sm">Click to see IELTS Band 8.5 synonyms</p>
          </CardContent>
        </Card>

        {/* Back of card */}
        <Card 
          className="absolute w-full h-full"
          style={{ 
            backfaceVisibility: 'hidden',
            transform: 'rotateY(180deg)'
          }}
        >
          <CardContent className="h-full flex flex-col items-center justify-center">
            <div className="text-center">
              <h3 className="text-xl font-semibold text-gray-900 mb-4">Band 8.5 Synonyms</h3>
              <div className="space-y-2">
                {card.synonyms.map((synonym, index) => (
                  <p key={index} className="text-gray-700">{synonym}</p>
                ))}
              </div>
              {card.example && (
                <div className="mt-4 pt-4 border-t border-gray-200">
                  <p className="text-sm text-gray-600 italic">{card.example}</p>
                </div>
              )}
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
